import Line from "./Line";

export type Mark = {
    positionX: number;
    positionY: number;
    svgString: string;
};
type CSSClipPath = string;

export default abstract class Frame {
    public abstract readonly bleedingArea: CSSClipPath;          // css clip-path (%)
    public abstract readonly shape: CSSClipPath;                 // css clip-path (%)
    public abstract readonly cutOutAreas: Array<CSSClipPath>;    // css clip-paths (%)
    public abstract readonly marks: Array<Mark>;       // SVG strings


 

    constructor(
        readonly maxWidth: number,  // mm
        readonly maxHeight: number  // mm
    ) {
    }
}